// Normally mainpage styles
$accent: #FF5C66
$main: #1B495B

html
	font-size: 16px

*
	transition: background-color 0.3s ease
	box-sizing: border-box

.button
	color: #fff
	border-radius: 6px
	background-color: $accent
	text-transform: uppercase
	padding: 25px 40px 25px 40px
	&:hover
		background-color: darken($accent, 10%)

.more
	font-family: "Montserrat", sans-serif
	text-transform: uppercase
	color: $accent
	font-size: 14px
	text-decoration: none
	position: relative
	&:hover
		text-decoration: underline

h1,h2,h3,h4,h5,h6
	text-transform: uppercase
	font-family: "Montserrat-Bold", sans-serif
	font-weight: bold
	color: $main
	span
		color: $accent

.out
	background-image: url(../img/Hero-Image.jpg)
	background-repeat: no-repeat
	background-position: 50% 0%
	background-size: cover

.header
	max-width: 940px  
	margin: 0 auto 
	margin-bottom: 70px
	max-height: calc(100vh + 100px)
	min-height: 1060px
	position: relative
	+r(1000)
		min-height: 900px
	+r(720)
		min-height: 700px
	&:after
		content: ' '
		display: block
		height: 178px
	&__label
		position: absolute
		top: 260px
		+r(1000)
			left: 20px
		+r(720)
			top: 300px
		h1
			font-size: 40px
			line-height: 54px
			+r(720)
				font-size: 32px
				line-height: 34px
		p
			font-size: 20px
			font-family: "OpenSans-Regular", sans-serif
			color: $main
			margin-bottom: 50px
	&__read-more
		font-family: "OpenSans-Bold", sans-serif
		font-size: 15px
		font-weight: bold
		text-decoration: none
		&:hover
			text-decoration: none

.icon-twitter
	+s(twitter)

.icon-fb
	+s(facebook)


.icon-arrow
	+s(Arrow)
	margin-left: 5px
	position: absolute
	top: 4px

.nav
	position: absolute
	right: 0
	top: 20px
	a
		font-family: "Montserrat-Regular", sans-serif
		font-size: calc(1rem + 0.1vw)
		text-transform: uppercase
		color: $main
		margin-left: 30px
		text-decoration: none
		transition: all .5s ease
		&:hover
			text-decoration: underline
	+r(1000)
		right: 20px
	+r(720)
		top: 90px
		left: 0px
		a
			display: block
			margin-bottom: 15px
			font-size: 0.9rem
			text-shadow: 0px 0px 3px #fff, 2px 2px 3px #fff, 0px 0px 2px #fff

.logo
	position: absolute
	top: -1px
	left: 0
	+r(1000)
		left: 20px

.skills
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	justify-content: space-around
	max-width: 940px  
	margin: 0 auto 
	margin-bottom: 70px
	+r(720)
		flex-direction: column
		text-align: center
		align-items: center
		margin-bottom: 0px
	&__item
		width: 300px
		margin: 0 10px
		+r(720)
			width: 320px
			margin: 0 0 45px 0
		h3
			font-size: 26px
			line-height: 26px
			margin-bottom: 25px
			+r(720)
				margin-bottom: 10px
			span
				color: $accent
		p 
			color: #9aa0ab
			font-family: "Myriad Pro", sans-serif
			font-weight: normal 
			font-size: 15px
			line-height: 22px
			margin-bottom: 25px
			+r(1000)
				font-size: 14px
			+r(720)
				font-size: 15px
				margin-bottom: 5px

.portfolio
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	justify-content: space-between
	max-width: 940px  
	margin: 0 auto 
	margin-bottom: 40px
	+r(720)
		justify-content: center
	&__img
		+r(720)
			display: none
	&__article
		+r(720)
			text-align: center
		h2
			font-size: 40px
		&_desc
			font-family: "OpenSans-Regular", sans-serif
			font-size: 18px
		p
			color: #9aa0ab
			font-family: "OpenSans-Regular", sans-serif
			font-size: 15px
			margin-top: 25px
			margin-bottom: 30px
			line-height: 22px

.team-container
	max-width: 940px  
	margin: 0 auto 
	margin-bottom: 120px
	position: relative
	+r(1000)
		padding-right: 20px
		padding-left: 20px
	+r(720)
		text-align: center
	h3
		font-size: 32px
		margin-bottom: 0
	p 
		color: #9aa0ab
		font-family: "OpenSans-Regular", sans-serif
		font-size: 16px
	.more-wrap
		display: block
		width: 50px
		position: absolute
		bottom: -40px
		right: 0
		+r(1000)
			right: 20px
		+r(720)
			position: relative
			bottom: 0
			right: 0
			margin: 0 auto



.team
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	justify-content: space-between
	margin-top: 30px
	+r(720)
		flex-direction: column
		text-align: center
		align-items: center

.team-item
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	align-items: center
	+r(720)
		margin-bottom: 20px
	&__img
		margin-right: 15px
	&__info
		h5
			font-size: 22px
		p
			font-size: 14px
			line-height: 18px
		.special
			color: $accent
			margin-bottom: 10px

.footer
	height: 120px
	border-top: 1px solid #E5EBEE
	max-width: 940px  
	margin: 0 auto 
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	justify-content: space-between
	align-items: center
	+r(1000)
		padding-left: 25px
		padding-right: 25px
	+r(720)
		flex-direction: column
		text-align: center
		align-items: center
		padding-top: 30px
		padding-bottom: 20px

.social
	position: relative
	a
		margin-right: 20px
		&:hover
			transform: scale(1.2)
	.icon-tw
		+s(tw)
	.icon-ins
		+s(ins)
	.icon-fb2
		+s(fb)
	.icon-gp
		+s(gp)

.copyright
	color: #B8C1C8