@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

input[type="submit"], button { cursor: pointer; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

.icon-badoo:before { content: ""; }

@font-face { font-family: "Montserrat-Bold"; src: url("fonts/Montserrat-Bold.woff2") format("woff2"), url("fonts/Montserrat-Bold.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: "Myriad Pro"; src: url("fonts/Myriad Pro.woff2") format("woff2"), url("fonts/Myriad Pro.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: "Myriad Pro Bold"; src: url("fonts/Montserrat-Bold.woff2") format("woff2"), url("fonts/Montserrat-Bold.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: "Montserrat-Regular"; src: url("fonts/Montserrat-Regular.woff2") format("woff2"), url("fonts/Montserrat-Regular.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: "OpenSans-Bold"; src: url("fonts/OpenSans-Bold.woff2") format("woff2"), url("fonts/OpenSans-Bold.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: "OpenSans-Regular"; src: url("fonts/OpenSans-Regular.woff2") format("woff2"), url("fonts/OpenSans-Regular.woff") format("woff"); font-weight: normal; font-style: normal; }
body { font-family: "Myriad Pro"; font-size: 16px; line-height: 1.6; }

html { font-size: 16px; }

* { transition: background-color 0.3s ease; box-sizing: border-box; }

.button { color: #fff; border-radius: 6px; background-color: #FF5C66; text-transform: uppercase; padding: 25px 40px 25px 40px; }
.button:hover { background-color: #ff2936; }

.more { font-family: "Montserrat", sans-serif; text-transform: uppercase; color: #FF5C66; font-size: 14px; text-decoration: none; position: relative; }
.more:hover { text-decoration: underline; }

h1, h2, h3, h4, h5, h6 { text-transform: uppercase; font-family: "Montserrat-Bold", sans-serif; font-weight: bold; color: #1B495B; }
h1 span, h2 span, h3 span, h4 span, h5 span, h6 span { color: #FF5C66; }

.out { background-image: url(../img/Hero-Image.jpg); background-repeat: no-repeat; background-position: 50% 0%; background-size: cover; }

.header { max-width: 940px; margin: 0 auto; margin-bottom: 70px; max-height: calc(100vh + 100px); min-height: 1060px; position: relative; }
@media only screen and (max-width: 1000px) { .header { min-height: 900px; } }
@media only screen and (max-width: 720px) { .header { min-height: 700px; } }
.header:after { content: " "; display: block; height: 178px; }
.header__label { position: absolute; top: 260px; }
@media only screen and (max-width: 1000px) { .header__label { left: 20px; } }
@media only screen and (max-width: 720px) { .header__label { top: 300px; } }
.header__label h1 { font-size: 40px; line-height: 54px; }
@media only screen and (max-width: 720px) { .header__label h1 { font-size: 32px; line-height: 34px; } }
.header__label p { font-size: 20px; font-family: "OpenSans-Regular", sans-serif; color: #1B495B; margin-bottom: 50px; }
.header__read-more { font-family: "OpenSans-Bold", sans-serif; font-size: 15px; font-weight: bold; text-decoration: none; }
.header__read-more:hover { text-decoration: none; }

.icon-twitter { background-image: url("../img/icons.png"); width: 20px; height: 16px; background-position: -24px -40px; }

.icon-fb { background-image: url("../img/icons.png"); width: 10px; height: 18px; background-position: -82px 0px; }

.icon-arrow { background-image: url("../img/icons.png"); width: 12px; height: 9px; background-position: -60px -22px; margin-left: 5px; position: absolute; top: 4px; }

.nav { position: absolute; right: 0; top: 20px; }
.nav a { font-family: "Montserrat-Regular", sans-serif; font-size: calc(1rem + 0.1vw); text-transform: uppercase; color: #1B495B; margin-left: 30px; text-decoration: none; transition: all 0.5s ease; }
.nav a:hover { text-decoration: underline; }
@media only screen and (max-width: 1000px) { .nav { right: 20px; } }
@media only screen and (max-width: 720px) { .nav { top: 90px; left: 0px; }
  .nav a { display: block; margin-bottom: 15px; font-size: 0.9rem; text-shadow: 0px 0px 3px #fff, 2px 2px 3px #fff, 0px 0px 2px #fff; } }

.logo { position: absolute; top: -1px; left: 0; }
@media only screen and (max-width: 1000px) { .logo { left: 20px; } }

.skills { display: -webkit-flex; display: -moz-flex; display: -ms-flex; display: -o-flex; display: flex; justify-content: space-around; max-width: 940px; margin: 0 auto; margin-bottom: 70px; }
@media only screen and (max-width: 720px) { .skills { flex-direction: column; text-align: center; align-items: center; margin-bottom: 0px; } }
.skills__item { width: 300px; margin: 0 10px; }
@media only screen and (max-width: 720px) { .skills__item { width: 320px; margin: 0 0 45px 0; } }
.skills__item h3 { font-size: 26px; line-height: 26px; margin-bottom: 25px; }
@media only screen and (max-width: 720px) { .skills__item h3 { margin-bottom: 10px; } }
.skills__item h3 span { color: #FF5C66; }
.skills__item p { color: #9aa0ab; font-family: "Myriad Pro", sans-serif; font-weight: normal; font-size: 15px; line-height: 22px; margin-bottom: 25px; }
@media only screen and (max-width: 1000px) { .skills__item p { font-size: 14px; } }
@media only screen and (max-width: 720px) { .skills__item p { font-size: 15px; margin-bottom: 5px; } }

.portfolio { display: -webkit-flex; display: -moz-flex; display: -ms-flex; display: -o-flex; display: flex; justify-content: space-between; max-width: 940px; margin: 0 auto; margin-bottom: 40px; }
@media only screen and (max-width: 720px) { .portfolio { justify-content: center; } }
@media only screen and (max-width: 720px) { .portfolio__img { display: none; } }
@media only screen and (max-width: 720px) { .portfolio__article { text-align: center; } }
.portfolio__article h2 { font-size: 40px; }
.portfolio__article_desc { font-family: "OpenSans-Regular", sans-serif; font-size: 18px; }
.portfolio__article p { color: #9aa0ab; font-family: "OpenSans-Regular", sans-serif; font-size: 15px; margin-top: 25px; margin-bottom: 30px; line-height: 22px; }

.team-container { max-width: 940px; margin: 0 auto; margin-bottom: 120px; position: relative; }
@media only screen and (max-width: 1000px) { .team-container { padding-right: 20px; padding-left: 20px; } }
@media only screen and (max-width: 720px) { .team-container { text-align: center; } }
.team-container h3 { font-size: 32px; margin-bottom: 0; }
.team-container p { color: #9aa0ab; font-family: "OpenSans-Regular", sans-serif; font-size: 16px; }
.team-container .more-wrap { display: block; width: 50px; position: absolute; bottom: -40px; right: 0; }
@media only screen and (max-width: 1000px) { .team-container .more-wrap { right: 20px; } }
@media only screen and (max-width: 720px) { .team-container .more-wrap { position: relative; bottom: 0; right: 0; margin: 0 auto; } }

.team { display: -webkit-flex; display: -moz-flex; display: -ms-flex; display: -o-flex; display: flex; justify-content: space-between; margin-top: 30px; }
@media only screen and (max-width: 720px) { .team { flex-direction: column; text-align: center; align-items: center; } }

.team-item { display: -webkit-flex; display: -moz-flex; display: -ms-flex; display: -o-flex; display: flex; align-items: center; }
@media only screen and (max-width: 720px) { .team-item { margin-bottom: 20px; } }
.team-item__img { margin-right: 15px; }
.team-item__info h5 { font-size: 22px; }
.team-item__info p { font-size: 14px; line-height: 18px; }
.team-item__info .special { color: #FF5C66; margin-bottom: 10px; }

.footer { height: 120px; border-top: 1px solid #E5EBEE; max-width: 940px; margin: 0 auto; display: -webkit-flex; display: -moz-flex; display: -ms-flex; display: -o-flex; display: flex; justify-content: space-between; align-items: center; }
@media only screen and (max-width: 1000px) { .footer { padding-left: 25px; padding-right: 25px; } }
@media only screen and (max-width: 720px) { .footer { flex-direction: column; text-align: center; align-items: center; padding-top: 30px; padding-bottom: 20px; } }

.social { position: relative; }
.social a { margin-right: 20px; }
.social a:hover { transform: scale(1.2); }
.social .icon-tw { background-image: url("../img/icons.png"); width: 20px; height: 16px; background-position: 0px -40px; }
.social .icon-ins { background-image: url("../img/icons.png"); width: 18px; height: 18px; background-position: -60px 0px; }
.social .icon-fb2 { background-image: url("../img/icons.png"); width: 10px; height: 18px; background-position: -82px -22px; }
.social .icon-gp { background-image: url("../img/icons.png"); width: 18px; height: 18px; background-position: -38px 0px; }

.copyright { color: #B8C1C8; }


