$icons: (0:0)
$icons: map-merge($icons,(Arrow: (X: -60px, Y:-22px, W: 12px, H: 9px, TW: 92px, TH: 74px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(codedby: (X: 0px, Y:-69px, W: 47px, H: 5px, TW: 92px, TH: 74px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(coderiver: (X: 0px, Y:-60px, W: 49px, H: 5px, TW: 92px, TH: 74px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(facebook: (X: -82px, Y:0px, W: 10px, H: 18px, TW: 92px, TH: 74px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(fb: (X: -82px, Y:-22px, W: 10px, H: 18px, TW: 92px, TH: 74px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(gp: (X: -38px, Y:0px, W: 18px, H: 18px, TW: 92px, TH: 74px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(ins: (X: -60px, Y:0px, W: 18px, H: 18px, TW: 92px, TH: 74px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(kav: (X: 0px, Y:0px, W: 34px, H: 36px, TW: 92px, TH: 74px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(tw: (X: 0px, Y:-40px, W: 20px, H: 16px, TW: 92px, TH: 74px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(twitter: (X: -24px, Y:-40px, W: 20px, H: 16px, TW: 92px, TH: 74px, IMG: '../img/icons.png')))


// Gets an attribute from the sass map
@function icon-attr($icon, $attr)
	$icon: map-get($icons, $icon)
	@return map-get($icon, $attr)

@mixin sprite($iconName)
	background-image: url(icon-attr($iconName, IMG))
	width: icon-attr($iconName, W)
	height: icon-attr($iconName, H)
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y)
@mixin sprite-position($iconName)
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y)
@mixin sprite-retina($iconName)
	background-image: url(icon-attr($iconName, IMG))
	$width: icon-attr($iconName, W)
	$height: icon-attr($iconName, H)
	width: $width/2
	height: $height/2
	$x: icon-attr($iconName, X)
	$y: icon-attr($iconName, Y)
	background-position: $x/2 $y/2
	$tw: icon-attr($iconName, TW) 	
	$th: icon-attr($iconName, TH)
	background-size: $tw/2 $th/2  	


@mixin s($i)
	+sprite($i)
@mixin sp($i)
	+sprite-position($i)
@mixin sr($i)
	+sprite-retina($i)	

